import React, { useState } from 'react';
import { useParams } from 'react-router-dom';
import AllFirstHomeAdmin from './AllFirstHomeAdmin';
import NotAllFirstHomeAdmin from './NotAllFirstHomeAdmin';
import RequestCall from './RequestCall';

const SuperAdminPanel = ({ content }) => {
    const [isAuth, setIsAuth] = useState(false)
    const [password, setPassword] = useState("")
    const screenWidth = window.innerWidth;
    const handleChange = (e) => {
        setPassword(e.target.value)
    }
    const handleSubmit = () => {
        if (password === '50114400') {
            setIsAuth(true)
        }
    }
    let { Id } = useParams()
    console.log(Id);
    return (
        <>

            {
                (isAuth && (content===true)) ? <NotAllFirstHomeAdmin /> :
                    (isAuth === true && Id === "notFirstHomeBuyer") ? <NotAllFirstHomeAdmin  /> :
                        (isAuth === true && Id === "firstHomeBuyer") ? <AllFirstHomeAdmin /> :
                            (isAuth === true && Id === "requestCall") ? <RequestCall /> :


                                <div className={`input-icon container d-block mx-auto mt-5 ${screenWidth < 576 ? 'w-100' : 'w-50'}`}>
                                    <h5 className='text-center'>Enter the password</h5>
                                    <input onChange={handleChange} type="text" className='form-control input-field w-75 d-block mx-auto' />

                                    <button onClick={handleSubmit} className='btn btn-primary main-button d-block mx-auto my-2'>Enter</button>
                                </div>
            }
        </>
    );
};

export default SuperAdminPanel;