import React from 'react';
import './Box.css';
import box1 from '../../assets/images/box1.png';
import box2 from '../../assets/images/box2.png';
import box3 from '../../assets/images/box3.png';
import box4 from '../../assets/images/box-4.png';

const boxData = [
    {
        img: box1,
        title: "Low Down Payments",
        description:"You can build your brand new home with as low as $2000 saving or even $0. Special personal loans designed for use as a deposit are available. If your rental history is good and you pay your bills on time, you likely qualify",
    },
    {
        img: box2,
        title: "Credit Score",
        description: "Your credit doesnâ€™t have to be perfect, we can work with if you had some problems in the past. Some lenders wont even look at your score and our broker partners have one of the best track records of making the impossible possible.",
    },
    {
        img: box3,
        title: "Low Interest Rates",
        description:"Donâ€™t me scared off by horror stories of high rates and repayments that simply arenâ€™t affordable. The rates on offer will be the best available to you on the market even as low as 1.99%",
    },
    {
        img: box4,
        title: 'Free Assessment',
        description:"We only need a 4 min phone call, in that short time we can tell you if we can get you hour home. Because we donâ€™t charge fees we get paid our lenders and banks we wont waste your time or ours."
    }
]

const Box = () => { 
    return (
        <>
            <div className='box-section'>
                <div className='mx-5'>
                   
                    <div className='row'>
                        {
                            boxData.map((data) => <div className='col-md-6 mb-3'>
                                  <div className='box-card'>
                    <div className='text-center mb-1'>
                        <img src={data.img} alt="" className='box-image' />
                    </div>
                    <div>
                                        <h3 className='box-title'>{ data.title}</h3>
                                        <p className='box-description'>{ data.description}</p>
                    </div>
                    </div>
                            </div>)
                        }
                    </div>    
                     
                </div>
                </div>
        
        </>
    );
};

export default Box;