import React, { useEffect, useState } from 'react'
import ReactHTMLTableToExcel from 'react-html-table-to-excel';
import { Link } from 'react-router-dom';


function AllFirstHomeAdmin() {
    const [database, setDatabase] = useState([])
  


    useEffect(() => {
        fetch('https://home-buyer-assistance55.herokuapp.com/api/v1/allFirstHome')
            .then(res => res.json())
            .then(allData => setDatabase(allData.data))
    }, [])

    return (
        <>
        
        <nav class="navbar navbar-expand-lg navbar-light bg-light">
                <div class="container">
                    <ul class="navbar-nav me-auto mb-2 mb-lg-0">
                        <li class="nav-item">
                            <Link className="btn btn-info me-2" to="/superAdmin/notFirstHomeBuyer">Not First Buyer</Link>
                            <Link className="btn btn-info me-2" to="/superAdmin/firstHomeBuyer">First Buyer</Link>
                            <Link className="btn btn-info me-2" to="/superAdmin/requestCall">Request Call</Link>
                        </li>
                    </ul>
                </div>
            </nav>

          
                    <div className='container my-4'>
                        <h1 className='text-center'>Download Data as Excel Sheet</h1>
                        <ReactHTMLTableToExcel
                            id="test-table-xls-button"
                            className="download-table-xls-button btn btn-primary d-block mx-auto my-3"
                            table="table-to-xls-1"
                            filename="First Home Buyer Data Sheet"
                            sheet="tablexls"
                            buttonText="Download as XLS" />
                        <h3 className='text-success'>All First Home Buyer Data Sheet</h3>

                        <table id="table-to-xls-1" className='table-striped table w-100 mx-auto'>
                            <tr className='text-center'>
                                <th>Name</th>
                                <th>Email</th>
                                <th>Telephone</th>
                                <th>Questions</th>
                            </tr>
                            {
                                // handleTable()


                                database.map(data => <tr className='text-center'>
                                    {console.log(data)}
                                    <td>{data.name}</td>
                                    <td>{data.email}</td>
                                    <td>{data.phone}</td>
                                    <td>

                                        <table className="table table-bordered ">
                                            <tr>
                                                <th>Question</th>
                                                <th>Answer</th>

                                            </tr>



                                            <tr className='text-center'>
                                                <td>{data.ques1.Q}</td>
                                                <td>{data.ques1.A}</td>
                                            </tr>
                                            <tr className='text-center'>
                                                <td>{data.ques2.Q}</td>
                                                <td>{data.ques2.A}</td>
                                            </tr>
                                            <tr className='text-center'>
                                                <td>{data.ques3.Q}</td>
                                                <td>{data.ques3.A}</td>
                                            </tr>
                                           
                                        </table>

                                    </td>


                                </tr>)


                            }

                        </table>
                    </div>
                    
        </>
    )
}

export default AllFirstHomeAdmin
