import React from 'react';
import Ques1 from '../ques1/Ques1';
import './HeroSection.css'
import Ques2 from './Ques2';
// import Ques3 from './Ques3';
import { useState } from "react";
import Progress from '../progress/Progress';
import Ques3 from './Ques3';

const HeroSection = () => {
    const [buttonBoxDisable, setButtonBoxDisable] = useState(false)
    const [buttonBoxDisable_2, setButtonBoxDisable_2] = useState(false)
    const [welcome, setWelcome] = useState(false)

    const [dataCheck, setDataCheck] = useState({
        step1: true,
        progressValue: 0


    })

    const [dataCheckOptions, setDataCheckOptions] = useState(!false)




    const optionNextButton = () => {

    }



    const welcomeBack = () => {

        setWelcome(false)
        setDataCheck({
            step1: true,
            progressValue: 0

        })
    }


    return (
        <div>
            <Progress progressValue={dataCheck.progressValue}></Progress>

            <section id="hero" className="containers relative">
                <div className="parent">
                    <div className="slider w-fit">

                        {
                            dataCheck.step1 ? <Ques1
                                dataCheckOptions={dataCheckOptions}
                                setDataCheckOptions={setDataCheckOptions}
                                optionNextButton={optionNextButton}
                                setDataCheck={setDataCheck}

                            /> : ''


                        }

                        {
                            dataCheckOptions ? <Ques2
                                setDataCheck={setDataCheck}
                                dataCheck={dataCheck}
                                buttonBoxDisable={buttonBoxDisable}
                                setButtonBoxDisable={setButtonBoxDisable}
                                setWelcome={setWelcome}
                            /> :
                                <Ques3
                                    setDataCheck={setDataCheck}
                                    dataCheck={dataCheck}
                                    dataCheckOptions={dataCheckOptions}
                                    setDataCheckOptions={setDataCheckOptions}
                                    buttonBoxDisable_2={buttonBoxDisable_2}
                                    setButtonBoxDisable_2={setButtonBoxDisable_2}
                                    setWelcome={setWelcome}
                                />
                        }


                    </div>
                </div>

                {
                    welcome ?
                        <div className="welcome">
                            <div>
                                <h1 className="font-bold">Welcome</h1>
                                <p>Your form has been submitted</p>
                                <button onClick={welcomeBack} className="btns mx-auto mt-3">Back</button>
                            </div>
                        </div> : ""
                }

            </section>
        </div>
    );
};

export default HeroSection;