import 'bootstrap/dist/css/bootstrap.min.css'
import 'bootstrap/dist/js/bootstrap.min.js'
import './App.css';
import Home from './pages/Home';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
import SuperAdminPanel from './components/admin/SuperAdminPanel';
import NotAllFirstHomeAdmin from './components/admin/NotAllFirstHomeAdmin';
import RequestCall from './components/admin/RequestCall';


function App() {
  return (
    <>
      <Router>
        <Switch>
          <Route exact path='/'>
            <Home />
          </Route>
          <Route exact path='/home'>
            <Home />
          </Route>

          <Route exact path='/superAdmin'>
            <SuperAdminPanel content={true} />
          </Route>
          <Route  exact path='/superAdmin/:Id'>
            <SuperAdminPanel content={false} />
          </Route>

  
        </Switch>
      </Router>
    </>
  );
}

export default App;



