import React, { useEffect, useState } from 'react'
import ReactHTMLTableToExcel from 'react-html-table-to-excel';
import { Link } from 'react-router-dom';


function RequestCall() {
    const [database, setDatabase] = useState([])
 


    useEffect(() => {
        fetch('https://home-buyer-assistance55.herokuapp.com/api/v1/allRequest')
            .then(res => res.json())
            .then(allData => setDatabase(allData.data))
    }, [])

    

    return (
        <>
        
        <nav class="navbar navbar-expand-lg navbar-light bg-light">
                <div class="container">
                    <ul class="navbar-nav me-auto mb-2 mb-lg-0">
                        <li class="nav-item">
                            <Link className="btn btn-info me-2" to="/superAdmin/notFirstHomeBuyer">Not First Buyer</Link>
                            <Link className="btn btn-info me-2" to="/superAdmin/firstHomeBuyer">First Buyer</Link>
                            <Link className="btn btn-info me-2" to="/superAdmin/requestCall">Request Call</Link>
                        </li>
                    </ul>
                </div>
            </nav>
       
                    <div className='container my-4'>
                        <h1 className='text-center'>Download Data as Excel Sheet</h1>
                        <ReactHTMLTableToExcel
                            id="test-table-xls-button"
                            className="download-table-xls-button btn btn-primary d-block mx-auto my-3"
                            table="table-to-xls-3"
                            filename="All Request a call Data Sheet"
                            sheet="tablexls"
                            buttonText="Download as XLS" />
                        <h3 className='text-success'>All Request a call Data Sheet</h3>

                        <table id="table-to-xls-3" className='table-striped table w-100 mx-auto'>
                            <tr className='text-center'>
                                <th>Name</th>
                                <th>Email</th>
                                <th>Telephone</th>
                              


                            </tr>
                            {
                                database.map(data => <tr className='text-center'>
                                    {console.log(data)}
                                    <td>{data.name}</td>
                                    <td>{data.email}</td>
                                    <td>{data.phone}</td>
                                   
                                </tr>)
                            }

                        </table>
                    </div>
                  
        </>
    )
}

export default RequestCall
