import React from 'react';
import './Secret.css';
import check2 from '../../assets/images/check2.svg'

const Secret = () => {
    return (
        <>
            <div className="secret-section">
            <div className="w-fit mx-auto">
                <div className="px-4">
                    <h2 className="secret-title mb-4" >More little secrets</h2>
                    <ul className="grid gap-3 mb-0 pl-0">
                        <li className="flex gap-2 items-center justify-start"><img src={check2} alt="icon" /> Your deposit can be as low as 2% savings</li>
                        <li className="flex gap-2 items-center justify-start"><img src={check2} alt="icon" /> You can even use a personal loan as a deposit</li>
                        <li className="flex gap-2 items-center justify-start"><img src={check2} alt="icon" /> Did you know that some banks will use a good rental history as savings?</li>
                        <li className="flex gap-2 items-center justify-start"><img src={check2} alt="icon" /> You can even use FHOG for a deposit</li>
                    </ul>
                </div>
            </div>
        </div>  
        </>
    );
};

export default Secret;