import React, { useState } from 'react';
import '../heroSection/HeroSection.css'
import emailjs from 'emailjs-com';


const Ques3 = ({ setDataCheck, dataCheck, setDataCheckOptions, dataCheckOptions, setButtonBoxDisable_2, buttonBoxDisable_2 ,setWelcome}) => {

    // const [buttonBoxDisable, setButtonBoxDisable] = useState(false)

    let currentProgressValue = dataCheck.progressValue

    const [activeData, setActiveData] = useState({
        ques11: true,
        ques3: false,

    })

    const nextButton = (step1 = false, progressValue, ques11, ques3) => {
        setDataCheck({ step1: step1, progressValue: progressValue })
        setActiveData({
            ques11: ques11,
            ques3: ques3,
        })
        setButtonBoxDisable_2(false)
    }




    const [select1, setSelect1] = useState({
        option1: false,
        option2: false,
        option3: false

    })

    const [data_2, setData_2] = useState({
        title: "Not my first home",
        name: "",
        email: '',
        phone: '',
        ques: {
            Q: 'How can I buy a home with no deposit?',
            A: ""
        }
    })

    const data_2_Collect = (e) => {

        const data = { ...data_2 }
        data[e.target.name] = e.target.value
        setData_2(data)
        console.log(data_2);
    }

    
    const ques111 = (option1, option2, option3, dataStr) => {
        setData_2({
            ...data_2,
            ques: {
                Q: 'How can I buy a home with no deposit?',
                A: dataStr
            }
        })
        setSelect1({
            option1: option1,
            option2: option2,
            option3: option3
        })
    }

    function sendEmail(data) {
        // e.preventDefault();
        const sendData = {
            name: data.name,
            email: data.email,
            phone: data.phone,
            title: "Not First Home Buyer"
        }
        console.log(sendData);

        emailjs.send('service_pawkrx8', 'template_fdfh5u3', sendData, 'e1TCAnug23CcluFnq')
            .then((result) => {
                console.log(result.text);

            }, (error) => {
                console.log(error.text);
            });
        // e.target.reset()
        setWelcome(true)

    }




    const submitData_2 = (e) => {
        setDataCheck({ step1: false, progressValue: 100 })
        const data = {...data_2}
        console.log(data, "ok");

        // e.preventDefault();

        const url = "https://home-buyer-assistance55.herokuapp.com/api/v1/newNotFirstHome"
        fetch(url, {
            method: 'POST',
            headers: {
                "Content-type": "application/json"
            },
            body: JSON.stringify(data)
        }).then(res => res.json())
            .then((data) => {
                console.log("Data inserted Successfully");
            })
            .catch(err => {
                console.log("Data was not inserted Successfully");
                console.log(err.message);

            })

            sendEmail(data)

    }



    return (
        <div id="go2" className="w-max go ">
            {
                activeData.ques11 ?
                    <div className="ques" id="ques11">



                        <div>
                            <h1 className="mb-3">Contact Details</h1>
                            <div className="form grid gap-2">
                                <div className="grid grid-cols-2 gap-2">
                                    <input onChange={data_2_Collect} name='name' type="text" placeholder="Name" style={{ padding: "19px 25px 18px" }} className="form-control" />
                                </div>
                                <input onChange={data_2_Collect} name='email' type="text" placeholder="Email" style={{ padding: "19px 25px 18px" }} className="form-control email validation" />
                                <small className="error d-none">This is not valid email</small>
                                <input onChange={data_2_Collect} name='phone' maxLength="15" type="text" placeholder="Phone" style={{ padding: "19px 25px 18px" }} className="form-control phone validation" />
                                <small className="error d-none">This is not valid phone number</small>
                                <div className="grid items-center gap-2" style={{ gridTemplateColumns: "25px 1fr" }}>

                                    {buttonBoxDisable_2 ?
                                        <input checked onClick={() => setButtonBoxDisable_2(false)} type="checkbox" className="check__ " hidden id="check123" />
                                        :
                                        <input onClick={() => setButtonBoxDisable_2(true)} type="checkbox" className="check__" hidden id="check123" />

                                    }
                                    <label htmlFor="check123">
                                        <svg xmlns="http://www.w3.org/2000/svg" fill="#fff" width="17" height="17" viewBox="0 0 24 24"><path d="M0 11c2.761.575 6.312 1.688 9 3.438 3.157-4.23 8.828-8.187 15-11.438-5.861 5.775-10.711 12.328-14 18.917-2.651-3.766-5.547-7.271-10-10.917z" /></svg>
                                    </label>
                                    <p className="text-white text-xs">By checking this box, I confirm that I am an authorized representative of Borrower and agree on Borrowerâ€™s behalf that Janover Inc. <a className="text-white hover:text-white" href="/">Privacy Policy</a> <a className="text-white hover:text-white" href="/">Terms</a></p>
                                </div>
                            </div>
                            <div className="conseq mt-3">
                                <button onClick={() => nextButton(true, currentProgressValue - 40, true, false)} className="btns back" >
                                    <svg width="12" height="12" viewBox="0 0 12 12" fill="none" xmlns="http://www.w3.org/2000/svg"><g clipPath="url(#clip0_121_2)"><path d="M6.988 10L3.9805 10L0 6L3.9805 2L6.988 2L3.0075 6L6.988 10ZM12 2H8.9925L5.012 6L8.9925 10H12L8.0195 6L12 2H12Z" fill="white" /></g><defs><clipPath id="clip0_121_2"><rect width="12" height="12" fill="white" /></clipPath></defs>
                                    </svg>
                                    Back
                                </button>



                                {
                                    buttonBoxDisable_2 ? <button onClick={() => nextButton(false, currentProgressValue + 40, false, true)} className="btns next"  >Next
                                        <svg xmlns="http://www.w3.org/2000/svg" fill="#fff" width="12" height="12" viewBox="0 0 24 24"><path d="M10.024 4h6.015l7.961 8-7.961 8h-6.015l7.961-8-7.961-8zm-10.024 16h6.015l7.961-8-7.961-8h-6.015l7.961 8-7.961 8z" /></svg>
                                    </button> :
                                        <button disabled onClick={() => nextButton(false, currentProgressValue + 40, false, true)} className="btns next"  >Next
                                            <svg xmlns="http://www.w3.org/2000/svg" fill="#fff" width="12" height="12" viewBox="0 0 24 24"><path d="M10.024 4h6.015l7.961 8-7.961 8h-6.015l7.961-8-7.961-8zm-10.024 16h6.015l7.961-8-7.961-8h-6.015l7.961 8-7.961 8z" /></svg>
                                        </button>
                                }



                            </div>
                        </div>

                    </div> :


                    activeData.ques3 ?


                        <div className="ques" id="ques2">
                            <div>
                                <h1>How can I buy a home with no deposit?</h1>
                                <div className="options">
                                    <div className="shadow-xl" onClick={() => ques111(true, false, false, "$15,000 Government Grant")}>
                                        <input className="form-input" id="input55" type="radio" hidden />
                                        <div className={`${select1.option1 ? 'checkMe hole' : "hole "}`}></div>
                                        <label >
                                            $15,000 Government Grant
                                        </label>
                                    </div>
                                    <div className="shadow-xl" onClick={() => ques111(false, true, false, " $15,000 Builders Cash Grant")}>
                                        <input className="form-input" id="input66" type="radio" name="ques" hidden />
                                        <div className={`${select1.option2 ? 'checkMe hole' : "hole"}`}></div>
                                        <label >
                                            $15,000 Builders Cash Grant
                                        </label>
                                    </div>
                                    <div className="shadow-xl span3rd" onClick={() => ques111(false, false, true, "Special low deposit loan assistance")}>
                                        <input className="form-input" id="input77" type="radio" name="ques" hidden />
                                        <div className={`${select1.option3 ? 'checkMe hole' : "hole"}`}></div>
                                        <label >
                                            Special low deposit loan assistance
                                        </label>
                                    </div>
                                </div>
                                <div className="conseq">
                                    <button onClick={() => nextButton(false, currentProgressValue - 40, true, false)} className="btns back" >
                                        <svg width="12" height="12" viewBox="0 0 12 12" fill="none" xmlns="http://www.w3.org/2000/svg"><g clipPath="url(#clip0_121_2)"><path d="M6.988 10L3.9805 10L0 6L3.9805 2L6.988 2L3.0075 6L6.988 10ZM12 2H8.9925L5.012 6L8.9925 10H12L8.0195 6L12 2H12Z" fill="white" /></g><defs><clipPath id="clip0_121_2"><rect width="12" height="12" fill="white" /></clipPath></defs>
                                        </svg>
                                        Back
                                    </button>

                                    {(select1.option1 || select1.option2 || select1.option3) ?
                                        <button onClick={() => submitData_2()} className="btns submit" >Submit</button> :
                                        <button disabled className="btns submit" >Submit</button>
                                    }
                                </div>
                            </div>
                        </div> : ''
            }
        </div>

    );
};

export default Ques3;