import React, { useState } from "react";
import emailjs from "emailjs-com";
const Ques2 = ({
  setDataCheck,
  dataCheck,
  buttonBoxDisable,
  setButtonBoxDisable,
  setWelcome,
}) => {
  let currentProgressValue = dataCheck.progressValue;

  const [data_1, setData_1] = useState({
    title: "First Home Buyer",
    name: "",
    email: "",
    phone: "",
    ques1: {
      Q: "What is your budget for your new home?",
      A: "",
    },
    ques2: {
      Q: "How much is your annual income?",
      A: "",
    },
    ques3: {
      Q: "How much deposit do you have saved?",
      A: "",
    },
  });

  const data_1_Collect = (e) => {
    const data = { ...data_1 };
    data[e.target.name] = e.target.value;
    setData_1(data);
    console.log(data_1);
  };

  const [activeData, setActiveData] = useState({
    ques2: true,
    ques3: false,
    ques4: false,
    ques5: false,
  });

  const nextButton = (
    step1 = false,
    progressValue,
    ques2,
    ques3,
    ques4,
    ques5
  ) => {
    setDataCheck({ step1: step1, progressValue: progressValue });
    setActiveData({
      ques2: ques2,
      ques3: ques3,
      ques4: ques4,
      ques5: ques5,
    });
    setButtonBoxDisable(false);
  };

  const [select2, setSelect2] = useState({
    option1: false,
    option2: false,
    option3: false,
  });
  const selectHandler2 = (option1, option2, option3, dataStr) => {
    setData_1({
      ...data_1,
      ques1: {
        Q: "What is your budget for your new home?",
        A: dataStr,
      },
    });
    setSelect2({
      option1: option1,
      option2: option2,
      option3: option3,
    });
  };
  const [select3, setSelect3] = useState({
    option1: false,
    option2: false,
    option3: false,
  });
  const selectHandler3 = (option1, option2, option3, dataStr) => {
    setData_1({
      ...data_1,
      ques2: {
        Q: "How much is your annual income?",
        A: dataStr,
      },
    });
    setSelect3({
      option1: option1,
      option2: option2,
      option3: option3,
    });
  };

  const [select4, setSelect4] = useState({
    option1: false,
    option2: false,
    option3: false,
    option4: false,
  });
  const selectHandler4 = (option1, option2, option3, option4, dataStr) => {
    setData_1({
      ...data_1,
      ques3: {
        Q: "How much deposit do you have saved?",
        A: dataStr,
      },
    });
    setSelect4({
      option1: option1,
      option2: option2,
      option3: option3,
      option4: option4,
    });
  };

  function sendEmail(data) {
    // e.preventDefault();
    const sendData = {
      name: data.name,
      email: data.email,
      phone: data.phone,
      title: "First Home Buyer",
    };
    console.log(sendData);

    emailjs
      .send(
        "service_pawkrx8",
        "template_fdfh5u3",
        sendData,
        "e1TCAnug23CcluFnq"
      )
      .then(
        (result) => {
          console.log(result.text);
        },
        (error) => {
          console.log(error.text);
        }
      );
    // e.target.reset()
    setWelcome(true);
  }

  const submitData_2 = (e) => {
    setDataCheck({ step1: false, progressValue: 100 });
    const data = { ...data_1 };
    console.log(data, "ok");

    e.preventDefault();

    const url =
      "https://home-buyer-assistance55.herokuapp.com/api/v1/newFirstHome";
    fetch(url, {
      method: "POST",
      headers: {
        "Content-type": "application/json",
      },
      body: JSON.stringify(data),
    })
      .then((res) => res.json())
      .then((data) => {
        console.log("Data inserted Successfully");
      })
      .catch((err) => {
        console.log("Data was not inserted Successfully");
        console.log(err.message);
      });
    sendEmail(data);
  };

  return (
    <div id="go1" className="w-fit go">
      {activeData.ques2 ? (
        <div className="ques" id="ques2">
          <div>
            <h1 className="mb-3">Contact Details</h1>
            <div className="form grid gap-2">
              <div className="grid grid-cols-2 gap-2">
                <input
                  onChange={data_1_Collect}
                  name="name"
                  type="text"
                  placeholder="Name"
                  style={{ padding: "19px 25px 18px" }}
                  className="form-control"
                />
              </div>
              <input
                onChange={data_1_Collect}
                name="email"
                type="text"
                placeholder="Email"
                style={{ padding: "19px 25px 18px" }}
                className="form-control email validation"
              />
              <small className="error d-none">This is not valid email</small>
              <input
                onChange={data_1_Collect}
                name="phone"
                maxLength="15"
                type="text"
                placeholder="Phone"
                style={{ padding: "19px 25px 18px" }}
                className="form-control phone validation"
              />
              <small className="error d-none">
                This is not valid phone number
              </small>
              <div
                className="grid items-center gap-2"
                style={{ gridTemplateColumns: "25px 1fr" }}
              >
                {buttonBoxDisable ? (
                  <input
                    checked
                    onClick={() => setButtonBoxDisable(false)}
                    type="checkbox"
                    className="check__ checked__"
                    hidden
                    id="check124"
                  />
                ) : (
                  <input
                    onClick={() => setButtonBoxDisable(true)}
                    type="checkbox"
                    className="check__"
                    hidden
                    id="check124"
                  />
                )}
                {/* <input type="checkbox" className="check__" hidden id="check124" /> */}

                <label htmlFor="check124">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    fill="#fff"
                    width="17"
                    height="17"
                    viewBox="0 0 24 24"
                  >
                    <path d="M0 11c2.761.575 6.312 1.688 9 3.438 3.157-4.23 8.828-8.187 15-11.438-5.861 5.775-10.711 12.328-14 18.917-2.651-3.766-5.547-7.271-10-10.917z" />
                  </svg>
                </label>
                <p className="text-white text-xs">
                  By checking this box, I confirm that I am an authorized
                  representative of Borrower and agree on Borrowerâ€™s behalf that
                  Janover Inc.{" "}
                  <a className="text-white hover:text-white" href="/">
                    Privacy Policy
                  </a>{" "}
                  <a className="text-white hover:text-white" href="/">
                    Terms
                  </a>
                </p>
              </div>
            </div>
            <div className="conseq">
              <button
                onClick={() =>
                  nextButton(
                    true,
                    currentProgressValue - 20,
                    true,
                    false,
                    false,
                    false
                  )
                }
                className="btns back"
              >
                <svg
                  width="12"
                  height="12"
                  viewBox="0 0 12 12"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <g clipPath="url(#clip0_121_2)">
                    <path
                      d="M6.988 10L3.9805 10L0 6L3.9805 2L6.988 2L3.0075 6L6.988 10ZM12 2H8.9925L5.012 6L8.9925 10H12L8.0195 6L12 2H12Z"
                      fill="white"
                    />
                  </g>
                  <defs>
                    <clipPath id="clip0_121_2">
                      <rect width="12" height="12" fill="white" />
                    </clipPath>
                  </defs>
                </svg>
                Back
              </button>
              {buttonBoxDisable ? (
                <button
                  onClick={() =>
                    nextButton(
                      false,
                      currentProgressValue + 20,
                      false,
                      true,
                      false
                    )
                  }
                  className="btns next"
                >
                  Next
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    fill="#fff"
                    width="12"
                    height="12"
                    viewBox="0 0 24 24"
                  >
                    <path d="M10.024 4h6.015l7.961 8-7.961 8h-6.015l7.961-8-7.961-8zm-10.024 16h6.015l7.961-8-7.961-8h-6.015l7.961 8-7.961 8z" />
                  </svg>
                </button>
              ) : (
                <button
                  disabled
                  onClick={() =>
                    nextButton(
                      false,
                      currentProgressValue + 20,
                      false,
                      true,
                      false
                    )
                  }
                  className="btns next"
                >
                  Next
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    fill="#fff"
                    width="12"
                    height="12"
                    viewBox="0 0 24 24"
                  >
                    <path d="M10.024 4h6.015l7.961 8-7.961 8h-6.015l7.961-8-7.961-8zm-10.024 16h6.015l7.961-8-7.961-8h-6.015l7.961 8-7.961 8z" />
                  </svg>
                </button>
              )}
            </div>
          </div>
        </div>
      ) : activeData.ques3 ? (
        <div className="ques" id="ques3">
          <div>
            <h1>What is your budget for your new home?</h1>
            <div className="options">
              <div
                onClick={() =>
                  selectHandler2(true, false, false, " Under $250,000")
                }
                className="shadow-xl"
              >
                <input
                  className="form-input"
                  id="input5"
                  type="radio"
                  name="ques3"
                  hidden
                />
                <div
                  className={`${select2.option1 ? "checkMe hole" : "hole "}`}
                ></div>
                <label>Under $250,000</label>
              </div>
              <div
                onClick={() =>
                  selectHandler2(false, true, false, " $250,000 - $500,000")
                }
                className="shadow-xl"
              >
                <input
                  className="form-input"
                  id="input6"
                  type="radio"
                  name="ques3"
                  hidden
                />
                <div
                  className={`${select2.option2 ? "checkMe hole" : "hole "}`}
                ></div>
                <label>$250,000 - $500,000</label>
              </div>
              <div
                onClick={() =>
                  selectHandler2(false, false, true, "Above $500,000")
                }
                className="shadow-xl span3rd"
              >
                <input
                  className="form-input"
                  id="input7"
                  type="radio"
                  name="ques3"
                  hidden
                />
                <div
                  className={`${select2.option3 ? "checkMe hole" : "hole "}`}
                ></div>
                <label>Above $500,000</label>
              </div>
            </div>
            <div className="conseq">
              <button
                onClick={() =>
                  nextButton(
                    false,
                    currentProgressValue - 20,
                    true,
                    false,
                    false,
                    false
                  )
                }
                className="btns back"
              >
                <svg
                  width="12"
                  height="12"
                  viewBox="0 0 12 12"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <g clipPath="url(#clip0_121_2)">
                    <path
                      d="M6.988 10L3.9805 10L0 6L3.9805 2L6.988 2L3.0075 6L6.988 10ZM12 2H8.9925L5.012 6L8.9925 10H12L8.0195 6L12 2H12Z"
                      fill="white"
                    />
                  </g>
                  <defs>
                    <clipPath id="clip0_121_2">
                      <rect width="12" height="12" fill="white" />
                    </clipPath>
                  </defs>
                </svg>
                Back
              </button>

              {select2.option1 || select2.option2 || select2.option3 ? (
                <button
                  onClick={() =>
                    nextButton(
                      false,
                      currentProgressValue + 20,
                      false,
                      false,
                      true,
                      false
                    )
                  }
                  className="btns next"
                >
                  Next
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    fill="#fff"
                    width="12"
                    height="12"
                    viewBox="0 0 24 24"
                  >
                    <path d="M10.024 4h6.015l7.961 8-7.961 8h-6.015l7.961-8-7.961-8zm-10.024 16h6.015l7.961-8-7.961-8h-6.015l7.961 8-7.961 8z" />
                  </svg>
                </button>
              ) : (
                <button
                  disabled
                  onClick={() =>
                    nextButton(
                      false,
                      currentProgressValue + 20,
                      false,
                      false,
                      true,
                      false
                    )
                  }
                  className="btns next"
                >
                  Next
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    fill="#fff"
                    width="12"
                    height="12"
                    viewBox="0 0 24 24"
                  >
                    <path d="M10.024 4h6.015l7.961 8-7.961 8h-6.015l7.961-8-7.961-8zm-10.024 16h6.015l7.961-8-7.961-8h-6.015l7.961 8-7.961 8z" />
                  </svg>
                </button>
              )}
            </div>
          </div>
        </div>
      ) : activeData.ques4 ? (
        <div className="ques" id="ques4">
          <div>
            <h1>How much is your annual income?</h1>
            <div className="options">
              <div
                onClick={() =>
                  selectHandler3(true, false, false, "Less than $40,000")
                }
                className="shadow-xl"
              >
                <input
                  className="form-input"
                  id="input8"
                  type="radio"
                  name="ques4"
                  hidden
                />
                <div
                  className={`${select3.option1 ? "checkMe hole" : "hole "}`}
                ></div>
                <label>Less than $40,000</label>
              </div>
              <div
                onClick={() =>
                  selectHandler3(false, true, false, "$40,000 - $80,000")
                }
                className="shadow-xl"
              >
                <input
                  className="form-input"
                  id="input9"
                  type="radio"
                  name="ques4"
                  hidden
                />
                <div
                  className={`${select3.option2 ? "checkMe hole" : "hole "}`}
                ></div>
                <label>$40,000 - $80,000</label>
              </div>
              <div
                onClick={() =>
                  selectHandler3(false, false, true, "Geater than $80,000")
                }
                className="shadow-xl span3rd"
              >
                <input
                  className="form-input"
                  id="input10"
                  type="radio"
                  name="ques4"
                  hidden
                />
                <div
                  className={`${select3.option3 ? "checkMe hole" : "hole "}`}
                ></div>
                <label>Geater than $80,000</label>
              </div>
            </div>
            <div className="conseq">
              <button
                onClick={() =>
                  nextButton(
                    false,
                    currentProgressValue - 20,
                    false,
                    true,
                    false,
                    false
                  )
                }
                className="btns back"
              >
                <svg
                  width="12"
                  height="12"
                  viewBox="0 0 12 12"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <g clipPath="url(#clip0_121_2)">
                    <path
                      d="M6.988 10L3.9805 10L0 6L3.9805 2L6.988 2L3.0075 6L6.988 10ZM12 2H8.9925L5.012 6L8.9925 10H12L8.0195 6L12 2H12Z"
                      fill="white"
                    />
                  </g>
                  <defs>
                    <clipPath id="clip0_121_2">
                      <rect width="12" height="12" fill="white" />
                    </clipPath>
                  </defs>
                </svg>
                Back
              </button>

              {select3.option1 || select3.option2 || select3.option3 ? (
                <button
                  onClick={() =>
                    nextButton(
                      false,
                      currentProgressValue + 20,
                      false,
                      false,
                      false,
                      true
                    )
                  }
                  className="btns next"
                >
                  Next
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    fill="#fff"
                    width="12"
                    height="12"
                    viewBox="0 0 24 24"
                  >
                    <path d="M10.024 4h6.015l7.961 8-7.961 8h-6.015l7.961-8-7.961-8zm-10.024 16h6.015l7.961-8-7.961-8h-6.015l7.961 8-7.961 8z" />
                  </svg>
                </button>
              ) : (
                <button
                  disabled
                  onClick={() =>
                    nextButton(
                      false,
                      currentProgressValue + 20,
                      false,
                      false,
                      false,
                      true
                    )
                  }
                  className="btns next"
                >
                  Next
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    fill="#fff"
                    width="12"
                    height="12"
                    viewBox="0 0 24 24"
                  >
                    <path d="M10.024 4h6.015l7.961 8-7.961 8h-6.015l7.961-8-7.961-8zm-10.024 16h6.015l7.961-8-7.961-8h-6.015l7.961 8-7.961 8z" />
                  </svg>
                </button>
              )}
            </div>
          </div>
        </div>
      ) : activeData.ques5 ? (
        <div className="ques" id="ques5">
          <div>
            <h1>How much deposit do you have saved?</h1>
            <div className="options">
              <div
                onClick={() => selectHandler4(true, false, false, false, "$0")}
                className="shadow-xl"
              >
                <input
                  className="form-input"
                  id="input11"
                  type="radio"
                  name="ques5"
                  hidden
                />
                <div
                  className={`${select4.option1 ? "checkMe hole" : "hole "}`}
                ></div>
                <label>$0</label>
              </div>
              <div
                onClick={() =>
                  selectHandler4(false, true, false, false, "Less than $10,000")
                }
                className="shadow-xl"
              >
                <input
                  className="form-input"
                  id="input12"
                  type="radio"
                  name="ques5"
                  hidden
                />
                <div
                  className={`${select4.option2 ? "checkMe hole" : "hole "}`}
                ></div>
                <label>Less than $10,000</label>
              </div>
              <div
                onClick={() =>
                  selectHandler4(false, false, true, false, "$10,000 - $50,000")
                }
                className="shadow-xl"
              >
                <input
                  className="form-input"
                  id="input13"
                  type="radio"
                  name="ques5"
                  hidden
                />
                <div
                  className={`${select4.option3 ? "checkMe hole" : "hole "}`}
                ></div>
                <label>$10,000 - $50,000</label>
              </div>
              <div
                onClick={() =>
                  selectHandler4(false, false, false, true, "More than $50,000")
                }
                className="shadow-xl"
              >
                <input
                  className="form-input"
                  id="input13"
                  type="radio"
                  name="ques5"
                  hidden
                />
                <div
                  className={`${select4.option4 ? "checkMe hole" : "hole "}`}
                ></div>
                <label>More than $50,000</label>
              </div>
            </div>
            <div className="conseq">
              <button
                onClick={() =>
                  nextButton(
                    false,
                    currentProgressValue - 20,
                    false,
                    false,
                    true,
                    false
                  )
                }
                className="btns back"
              >
                <svg
                  width="12"
                  height="12"
                  viewBox="0 0 12 12"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <g clipPath="url(#clip0_121_2)">
                    <path
                      d="M6.988 10L3.9805 10L0 6L3.9805 2L6.988 2L3.0075 6L6.988 10ZM12 2H8.9925L5.012 6L8.9925 10H12L8.0195 6L12 2H12Z"
                      fill="white"
                    />
                  </g>
                  <defs>
                    <clipPath id="clip0_121_2">
                      <rect width="12" height="12" fill="white" />
                    </clipPath>
                  </defs>
                </svg>
                Back
              </button>
              {select4.option1 ||
              select4.option2 ||
              select4.option3 ||
              select4.option4 ? (
                <button onClick={submitData_2} className="btns submit">
                  Submit
                </button>
              ) : (
                <button disabled className="btns submit">
                  Submit
                </button>
              )}
            </div>
          </div>
        </div>
      ) : (
        ""
      )}
    </div>
  );
};

export default Ques2;
