import pic1 from "../../img/pic-1.svg";
import pic2 from "../../img/pic-2.svg";
import curve from "../../img/curve.svg";
import { useState } from "react";
import '../heroSection/HeroSection.css'
import emailjs from 'emailjs-com';


function Ques1({ optionNextButton, setDataCheck, setDataCheckOptions, dataCheckOptions }) {

    const [tost, setTost] = useState(false)
    const myTimeout = () => {
        
        setTimeout(function(){setTost(false)}, 3000);
    }

    const [requestData, setRequestData] = useState({
        title: "Request a call",
        name: "",
        email: '',
        phone: '',
    })

    const requestCallHandler = (e) => {

        const data = { ...requestData }
        data[e.target.name] = e.target.value
        setRequestData(data)
    }

    function sendEmail(data) {
        // e.preventDefault();
        const sendData = {
            name: data.name,
            email: data.email,
            phone: data.phone,
            title: "Request a call"
        }

        emailjs.send('service_pawkrx8', 'template_fdfh5u3', sendData, 'e1TCAnug23CcluFnq')
            .then((result) => {
                console.log(result.text);

            }, (error) => {
                console.log(error.text);
            });
        // e.target.reset()

    }




    const handleRqeustSubmit = (e) => {
        console.log(requestData);
       
        
        e.preventDefault();

        const data = {...requestData}
 
        const url = "https://home-buyer-assistance55.herokuapp.com/api/v1/newRequest"
        fetch(url, {
            method: 'POST',
            headers: {
                "Content-type": "application/json"
            },
            body: JSON.stringify(data)
        }).then(res => res.json())
            .then((res) => {
                console.log('Data inserted Successfully')
            })
            .catch(err => {
                console.log("Data was not inserted Successfully");
                console.log(err.message);

            })
            e.target.reset()
            setTost(true)
            myTimeout()
            sendEmail(data)
    }


    const nextButton = () => {
        setDataCheck({ step1: false, progressValue: 20 })
    }



    return (
        <>
            {
                tost ? <div className="toasts">
                    Requested for a call
                </div> : ""
            }

            <div className="ques" id="ques1">
                <h1>Want to buy a house, are you eligible?</h1>
                <div className="ques1_main gap-5 grid">
                    <div>
                        <form className="options">
                            <div onClick={() => setDataCheckOptions(true)}>

                                <input className="form-input" id="input1" type="radio" name="ques1" checked hidden />


                                <label className="h-auto shadow-xl ">
                                    <div className={`${dataCheckOptions ? 'checkMe hole' : "hole"}`}></div>
                                    <img src={pic1} alt="picture1" />
                                    <p>
                                        First Home Buyer
                                    </p>
                                </label>
                                <div className="curve_text">
                                    <img src={curve} alt="curve" />
                                    <b className="block text-sm">Can I buy a house with no $0 Deposit?</b>
                                    <span className="text-xs">2 Mins to see if you qualify Same day call back</span>
                                </div>
                            </div>
                            <div onClick={() => setDataCheckOptions(false)}>
                                <input className="form-input" id="input2" type="radio" name="ques1" hidden />
                                <label className="h-auto shadow-xl">
                                    <div className={`${!dataCheckOptions ? 'checkMe hole' : "hole"}`}></div>
                                    <img src={pic2} alt="picture2" />
                                    <p>
                                        Not my first home
                                    </p>
                                </label>
                            </div>
                        </form>
                        <div className="conseq">
                            <button onClick={() => nextButton()} className="btns next">Next
                                <svg xmlns="http://www.w3.org/2000/svg" fill="#fff" width="12" height="12" viewBox="0 0 24 24"><path d="M10.024 4h6.015l7.961 8-7.961 8h-6.015l7.961-8-7.961-8zm-10.024 16h6.015l7.961-8-7.961-8h-6.015l7.961 8-7.961 8z" /></svg>
                            </button>
                        </div>
                    </div>

                    <div>
                        <h2 className="text-white text-center mb-3">Same day call back</h2>
                        <form onSubmit={handleRqeustSubmit} className="same_day_form" >
                            <input onChange={requestCallHandler} name="name" required type="text" placeholder="Name" />
                            <input onChange={requestCallHandler} name="phone" required type="number" placeholder="Phone" />
                            <input onChange={requestCallHandler} name="email" required type="email" placeholder="Email" />
                            <button className="btns w-fit mx-auto" type="submit">Request a call</button>
                        </form>
                    </div>

                </div>
            </div>
        </>
    );
}

export default Ques1;